html {
  background-color: #93b7be;
  text-align: center;
    font-family: "Azeret Mono", monospace;

}
#quote-box {
  margin-top: 5vw;
  margin-left: auto;
  margin-right: auto;
  width: 70vw;
  height: 30vw;
  background-color: white;
  position: relative;
}

#quote-container {
  font-size: 3vw;
  color: #93b7be;
  padding: 3vh;
}

.btn {
  position: absolute;
  bottom: 1vw;
  background-color: #747578;
  border: none;
  color: white;
  border-radius: 6%;
  height: 3vw;
}

#author {
  margin-top: 1.5vw;
}

#new-quote {
  width: auto;
  right: 4vw;
  font-size: 1.2vw;
  cursor: pointer;
}

#tweet-btn {
  position: absolute;
  bottom: 1vw;
  left: 4vw;
  height: 3vw;
  width: 3vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

#twitter-icon {
  color: white;
  font-size: 1.5vw;
}

@media (max-width: 450px) {
  #quote-container {
    font-size: 2.5vw;
  }
}